import React, { lazy, useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { fireAuth } from "../fireApi";
import logo from './garuda.png'
import KL from './KL3.png'
import Kemenko from './kemenko.png'
import Kemenperin from './kemenperin.png'
import Kemendag from './kemendag.png'
import Kementan from './kementan.png'
import Kkp from './kkp.png'
import Bps from './bps.png'
import Bkpm from './bkpm.png'
